@import '../../../../imports';

.bar-chart-x-axis {
  @include pin-full;

  line {
    pointer-events: none;
    stroke: $gray;
    stroke-width: 1;
  }

  text {
    @include css-variable(fill, text-medium);
    text-anchor: middle;
    font-size: 12px;
  }
}
